<template>
  <div>
    <v-card
        class="mt-4 mb-5 pa-2"
        width="80vw"
        height="80vh"
        style="border-radius: 10px;"
        hover
        color="primary"
    >
        <v-toolbar color="primary" flat>
            <v-toolbar-side-icon>
              <v-icon color="white">
                fas fa-map-marked-alt
              </v-icon>
            </v-toolbar-side-icon>
            <v-toolbar-title class="white--text">
                Path Travelled
            </v-toolbar-title>

            <v-spacer></v-spacer>

            <v-btn color='secondary'>
                <v-icon class="mr-2">
                    fas fa-file-export
                </v-icon>
                Export As PNG
            </v-btn>
        </v-toolbar>
       <GmapMap
            ref="map"
            :center="user.farmCenter"
            :zoom="13"
            map-type-id="satellite"
            style="width: 100%; height: 91%;"
        >
            <gmap-polyline
                v-if="paths.length > 0"
                :path="paths"
                :options="polyOptions"
            ></gmap-polyline>

        </GmapMap>
    </v-card>
        <v-range-slider
          :tick-labels="labels"
          :value="[0, 1]"
          always-dirty
          min="0"
          max="4"
          thumb-label
          thumb-size="64"
          ticks="always"
        >
          <template
            slot="thumb-label"
            slot-scope="props"
          >
            <span>
              {{ timeWindow(props.value) }}
            </span>
          </template>
        </v-range-slider>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'GPSTracks',
  computed: {
    ...mapState([
      'user',
      'paddocks',
      'tsData',
    ]),

    paths() {
      const paths = [];
      const dataLen = this.tsData.length;
      for (let i = 0; i < dataLen; i += 1) {
        //const coordinate = this.tsData[i].data.gps.split(',');
        paths.push({ lat: parseFloat(this.tsData[i].data.lat), lng: parseFloat(this.tsData[i].data.lng) });
      }
    
      return paths;
    },
  },
  data: () => ({
    polyOptions: {
      strokeColor: '#74B3CE',
    },
    selectedWindow: 0,
    labels: [
      'Now',
      '6 hours',
      '12 hours',
      '18 hours',
      '24 hours'
    ]
  }),
  methods: {
    timeWindow(val) {
        return this.labels[val];
      }
  }
};
</script>
