<template>
    <div class="data">
        <v-layout
            column
            align-center
            justify-center
        >
            <TSGraph v-if="selected.type === 'tank' || selected.type === 'motion'"/>

            <GPSTracks v-if="tracks.includes(selected.type)"/>

              <v-card
                class="mt-2 pa-2"
                width="80vw"
                hover
                style="border-radius: 10px;"
                color="primary"
            >
                <v-toolbar color="primary" flat>
                    <v-toolbar-side-icon>
                        <v-icon color="white">
                            fas fa-table
                        </v-icon>
                    </v-toolbar-side-icon>
                    <v-toolbar-title class="white--text">
                        Data Log
                    </v-toolbar-title>

                    <v-spacer></v-spacer>

                    <v-btn color='secondary'>
                        <v-icon class="mr-2">
                            fas fa-file-export
                        </v-icon>
                        Export As CSV
                    </v-btn>
                </v-toolbar>
                <v-data-table
                    :headers="headers"
                    :items="tsData"
                >   
                    <template slot="no-data">
                      <v-alert :value="true"
                        color="error"
                        icon="fas fa-empty-set"
                      >
                        {{ selected.name }} has not recorded any data yet!
                      </v-alert>
                    </template>
                    <template slot="items" slot-scope="props">
                        <td v-for="header in headers"
                            :key="header.value"
                        >
                            {{ props.item[header.value] }}
                            {{ props.item.data[header.value] }}
                        </td>
                    </template>
                </v-data-table>
            </v-card>

        </v-layout>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import TSGraph from './addons/TSGraph.vue';
import GPSTracks from './addons/GPSTracks.vue';

export default {
  name: 'AssetData',
  components: {
    TSGraph,
    GPSTracks,
  },
  computed: {
    ...mapState([
      'selected',
      'tsData',
    ]),

    headers() {
      const diagFields = [
        'RSSI',
        'SNR',
        'alarm',
        'batteryV',
        'outdated',
        'trackable',
        'hAcc',
        'msss',
        'numSV',
        'pdop',
        'species',
        'ttff',
        '__ob__',
        'serialNumber'
      ];

      if (!this.selected.data.trackable) {
        diagFields.push('gps', 'lat', 'lng');
      }

      const headers = [
        { text: 'Date', align: 'left', value: 'date' },
        { text: 'Time', align: 'left', value: 'time' },
      ];
      const attributes = Object.getOwnPropertyNames(this.selected.data);
      const attrLen = attributes.length;
      for (let i = 0; i < attrLen; i += 1) {
        if (!diagFields.includes(attributes[i])) {
          headers.push({ text: attributes[i], value: attributes[i] });
        }
      }
      return headers;
    },
  },
  data: () => ({
    tracks: [
      'sheep',
      'cow',
      'pig',
      'quad',
      'tractor',
    ],
  }),
};
</script>
