<template>
  <v-card
    class="ma-4"
    width="80vw"
    style="border-radius: 10px;"
    height="67vh"
    hover
  >
    <v-toolbar color="primary"
      class="mb-2"
    >
      <v-toolbar-title class="white--text">
        Data Chart
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn color='secondary'>
          <v-icon class="mr-2">
              fas fa-file-export
          </v-icon>
          Export As PNG
      </v-btn>
    </v-toolbar>

    <GChart
      v-if="selected.type === 'tank'"
      type="AreaChart"
      :data="chartSeries"
      :options="tankOptions"
      class="chart"
    ></GChart>

    <GChart
      v-if="selected.type === 'motion'"
      type="ColumnChart"
      :data="chartSeries"
      :options="motionOptions"
      class="chart"
    ></GChart>
  </v-card>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  name: 'TSGraph',
  computed: {
    ...mapState({
      series: 'tsData',
      selected: 'selected',
    }),

    chartSeries() {
      const seriesData = [];
      if (this.selected.type === 'tank') {
        seriesData.push(['Time', 'Percent Full']);
        const dataLen = this.series.length;
        for (let i = 0; i < dataLen; i += 1) {
          seriesData.push([this.series[i].time, this.series[i].data.percentFull]);
        }
      } else if (this.selected.type === 'motion') {
        seriesData.push(['Time', 'Motion Detection Length']);
        const seriesLen = this.series.length;
        for (let i = 0; i < seriesLen; i += 1) {
          seriesData.push([this.series[i].time, this.series[i].data.lengthOfDetection]);
        }
      }
      return seriesData;
    },
  },
  data: () => ({
    tankOptions: {
      title: 'Water Levels over 24 Hours',
      hAxis: {
        title: 'Time',
        titleTextStyle: {
          color: '#004346',
        },
      },
      vAxis: {
        maxValue: 100,
        minValue: 0,
        title: 'Percent Full',
        titleTextStyle: {
          color: '#004346',
        },
      },
      colors: ['#74B3CE'],
    },
    motionOptions: {
      title: 'Duration of Motion Detection over 24 Hours',
      hAxis: {
        title: 'Time',
        titleTextStyle: {
          color: '#004346',
        },
      },
      vAxis: {
        title: 'Duration of Motion (seconds)',
        titleTextStyle: {
          color: '#004346',
        },
      },
      colors: ['#74B3CE'],
    },
  }),
};
</script>

<style scoped>
  .chart {
    width: 95%;
    height: 70%;
  }

</style>
